export default {
	data() {
		return {
			mobile: {
				phone: '',
				code: ''
			},
			Unbind: true,
			// 验证码
			safety: {
				time: 60,
				state: false,
				interval: ''
			},
			loginMobile: false,
			phoneReg: /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/,
		}
	},
	computed: {
		btnText() {
			return this.Unbind ? "下一步" : "保存"
		},
		holder() {
			return this.Unbind ? "请输入原手机号解绑..." : "请输入新手号..."
		}
	},
	watch: {
		/**
		 * 监听手机登录数值
		 */
		mobile: {
			handler(newValue) {
				if (this.phoneReg.test(newValue.phone) && newValue.code.length === 6) {
					this.loginMobile = true;
				} else {
					this.loginMobile = false;
				}
			},
			deep: true
		},
			
	},
	methods: {
		// 登录按钮点击执行
		fnLogin() {
			console.log(JSON.stringify(this.mobile));
			let title;

			if (this.Unbind) {
				title = "解绑中...";
				setTimeout(() => {
					uni.hideLoading()
					this.Unbind = false;
					this.mobile.phone = "";
					this.mobile.code = "";
				}, 2000)
			} else {
				title = "保存中..."
				setTimeout(() => {
					uni.hideLoading()
					uni.navigateTo({
						url: "/pages/shop/changeSuccess"
					})
				}, 2000)
			}
			uni.showLoading({
				title,
				mask: true
			})


		},
		// 获取验证码
		fnGetPhoneCode() {
			if (this.phoneReg.test(this.mobile.phone)) {
				uni.showToast({
					title: "正在发送验证码",
					icon: "loading",
					success: () => {
						// 成功后显示倒计时60s后可在点击
						this.safety.state = true;
						// 倒计时
						this.safety.interval = setInterval(() => {
							if (this.safety.time-- <= 0) {
								this.safety.time = 60;
								this.safety.state = false;
								clearInterval(this.safety.interval);
							}
						}, 1000);
						uni.showToast({
							title: "发送成功",
							icon: "success"
						})
					}
				})
			} else {
				uni.showToast({
					title: "手机号不正确",
					icon: "none"
				})
			}
		},
		_switchToAuth() {
			uni.navigateTo({
				url: "/pages/login/authentication"
			})
		}
	}
}
